___CSS_LOADER_EXPORT___.push([module.id, `*, *::before, *::after {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html, body {
    height: 100%;
    width: 100%;
    overflow-x: hidden;
}

.main-container {
    padding: var(--size-padding-medium);
    display: flex;
    flex-direction: column;
    align-items: stretch;
    width: calc(100vw - 2 * var(--size-padding-medium));
    max-width: 100%;
}

.textarea {
    width: 100%;
    padding: var(--size-padding-medium);
    border: 1px solid var(--color-border);
    border-radius: var(--size-border-radius);
    margin: var(--size-margin-small) 0;
    background-color: var(--color-background-light);
    resize: vertical;
    min-height: 30px;
    overflow: auto;
}

.horizontal-section {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: nowrap;
    margin-top: var(--size-margin-medium);
    margin-bottom: var(--size-margin-medium);
}

#compression-buttons {
    display: flex;
    align-items: center;
}

#compression-buttons .label {
    margin-right: var(--size-margin-small);
}

.compression-button,
.webpage-button {
    height: var(--size-element-height);
    padding: 0 var(--size-padding-large);
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    border: none;
    border-radius: var(--size-border-radius);
    cursor: pointer;
    transition: background-color var(--transition-speed);
    font-size: var(--size-font-default);
    white-space: nowrap;
    margin-right: var(--size-margin-small);
    min-width: 80px;
}

.compression-button {
    background-color: var(--color-background-medium);
    color: var(--color-text);
}

.compression-button:hover {
    background-color: var(--color-background-dark);
}

.compression-button.selected {
    background-color: var(--color-background-active);
    color: var(--color-text-light);
}

.webpage-button {
    background-color: var(--color-button);
    color: var(--color-text-light);
    min-width: 120px;
}

.webpage-button:hover {
    background-color: var(--color-button-hover);
}

.counters-container {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: var(--size-margin-small);
}

.counter {
    display: flex;
    align-items: center;
    margin: 0 var(--size-margin-small);
}

.counter .label {
    margin-right: var(--size-margin-small);
    white-space: nowrap;
}

.input {
    width: 60px;
    height: var(--size-element-height);
    padding: 0 var(--size-padding-small);
    background-color: var(--color-background-medium);
    color: var(--color-text);
    border: none;
    border-radius: var(--size-border-radius);
    text-align: center;
    font-size: var(--size-font-default);
}

.json-types-container {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: var(--size-margin-medium);
}

.instructions {
    margin-bottom: var(--size-margin-medium);
    display: flex;
    justify-content: center;
    align-items: center;
    height: 50px;
    text-align: center;
}

.instructions p {
    font-size: var(--size-font-large);
    color: var(--color-text-muted);
    margin: 0;
}

.row {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin: var(--size-margin-medium) 0;
    width: 100%;
    min-height: calc(var(--size-element-height) + 2 * var(--size-margin-small));
}

.box {
    position: relative;
    display: flex;
    align-items: center;
    height: 30px;
    margin: var(--uniform-margin);
    cursor: grab;
    user-select: none;
    border-radius: var(--border-radius);
    transition: background-color var(--transition-speed), 
                transform 0.3s ease-in-out, 
                opacity 0.3s ease-in-out;
    overflow: visible;
    will-change: transform, opacity;
}

.box:hover {
    z-index: 1;
}

.box:active {
    cursor: grabbing;
}

.box.hidden {
    opacity: 0.5;
}

.box.dragging {
    opacity: 0.8;
    transform: scale(1.05);
    z-index: 1000;
}

.box .icon {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 30px;
    height: 100%;
    background-color: var(--icon-bg-color);
    cursor: grab;
    flex-shrink: 0;
    padding: 0;
}

#row2 .box .icon {
    background-color: var(--icon-bg-color-row2);
}

#row2 .box .right-icon {
    background-color: var(--right-icon-bg-color);
}

.box .left-icon {
    border-top-left-radius: var(--border-radius);
    border-bottom-left-radius: var(--border-radius);
}

.box .left-icon.icon-file::before {
    content: '';
    display: inline-block;
    background: var(--icon-file) no-repeat center center / contain;
    width: 18px;
    height: 18px;
}

.box .left-icon.icon-folder::before {
    content: '';
    display: inline-block;
    background: var(--icon-folder) no-repeat center center / contain;
    width: 18px;
    height: 18px;
}

.box .text {
    flex: 1;
    display: flex;
    align-items: center;
    background-color: var(--text-bg-color);
    cursor: grab;
    height: 100%;
    padding-left: 8px;
    padding-right: 8px;
    color: var(--color-text-light);
    font-size: 0.9em;
}

#row1 .box .text {
    border-top-right-radius: var(--border-radius);
    border-bottom-right-radius: var(--border-radius);
}

#row2 .box .text {
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
}

.box .right-icon {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 30px;
    height: 100%;
    background-color: var(--right-icon-bg-color);
    border-top-right-radius: var(--border-radius);
    border-bottom-right-radius: var(--border-radius);
    flex-shrink: 0;
    padding: 0;
}

.box .right-icon::before {
    content: '';
    display: inline-block;
    background: var(--icon-eye-open) no-repeat center center / contain;
    width: 18px;
    height: 18px;
}

.box .right-icon.open-eye::before {
    background-image: var(--icon-eye-open);
}

.box .right-icon.closed-eye::before {
    background-image: var(--icon-eye-closed);
}

.line-container {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    margin: var(--size-margin-medium) 0;
    width: 100%;
    height: 40px;
}

.line {
    flex: 1;
    height: 2px;
    background-color: var(--color-border);
}

.garbage-icon {
    width: 40px;
    height: 40px;
    background: var(--icon-garbage) no-repeat center center / 28px 28px;
    border-radius: 50%;
    cursor: pointer;
    margin: 0 10px;
    flex-shrink: 0;
    transition: background-image 0.3s ease;
}

body[data-dragging="true"] .garbage-icon:hover {
    background-image: var(--icon-garbage-open);
}

.placeholder {
    border: 2px dashed #ccc;
    background-color: rgba(204, 204, 204, 0.3);
    margin: var(--uniform-margin);
    border-radius: var(--border-radius);
}

@media (max-width: 890px) {
    .horizontal-section {
        flex-direction: column;
        align-items: stretch;
    }

    .horizontal-section > * {
        margin-bottom: var(--size-margin-small);
    }

    #compression-buttons {
        flex-direction: column;
        align-items: stretch;
    }

    .compression-button,
    .webpage-button {
        width: 100%;
        max-width: 200px;
        margin: 0 auto var(--size-margin-small) auto;
    }

    .counters-container {
        flex-direction: row;
        justify-content: center;
        width: 100%;
    }

    .counter {
        flex: 1;
        justify-content: center;
    }

    .input {
        width: 60px;
    }

    .json-types-container, .json-types-list {
        padding: 0 var(--size-padding-medium);
    }

    .main-container {
        height: 100%;
    }
}

@media (max-width: 500px) {
    .button, .input, .compression-button, .textarea {
        padding: 0 var(--size-padding-small);
    }

    .filetype-input {
        font-size: var(--size-font-default);
    }
}`, "",{"version":3,"sources":["webpack://./src/webview/styles/webview.css"],"names":[],"mappings":"AAEA;IACI,SAAS;IACT,UAAU;IACV,sBAAsB;AAC1B;;AAEA;IACI,YAAY;IACZ,WAAW;IACX,kBAAkB;AACtB;;AAEA;IACI,mCAAmC;IACnC,aAAa;IACb,sBAAsB;IACtB,oBAAoB;IACpB,mDAAmD;IACnD,eAAe;AACnB;;AAEA;IACI,WAAW;IACX,mCAAmC;IACnC,qCAAqC;IACrC,wCAAwC;IACxC,kCAAkC;IAClC,+CAA+C;IAC/C,gBAAgB;IAChB,gBAAgB;IAChB,cAAc;AAClB;;AAEA;IACI,aAAa;IACb,8BAA8B;IAC9B,mBAAmB;IACnB,iBAAiB;IACjB,qCAAqC;IACrC,wCAAwC;AAC5C;;AAEA;IACI,aAAa;IACb,mBAAmB;AACvB;;AAEA;IACI,sCAAsC;AAC1C;;AAEA;;IAEI,kCAAkC;IAClC,oCAAoC;IACpC,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,kBAAkB;IAClB,YAAY;IACZ,wCAAwC;IACxC,eAAe;IACf,oDAAoD;IACpD,mCAAmC;IACnC,mBAAmB;IACnB,sCAAsC;IACtC,eAAe;AACnB;;AAEA;IACI,gDAAgD;IAChD,wBAAwB;AAC5B;;AAEA;IACI,8CAA8C;AAClD;;AAEA;IACI,gDAAgD;IAChD,8BAA8B;AAClC;;AAEA;IACI,qCAAqC;IACrC,8BAA8B;IAC9B,gBAAgB;AACpB;;AAEA;IACI,2CAA2C;AAC/C;;AAEA;IACI,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,6BAA6B;AACjC;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,kCAAkC;AACtC;;AAEA;IACI,sCAAsC;IACtC,mBAAmB;AACvB;;AAEA;IACI,WAAW;IACX,kCAAkC;IAClC,oCAAoC;IACpC,gDAAgD;IAChD,wBAAwB;IACxB,YAAY;IACZ,wCAAwC;IACxC,kBAAkB;IAClB,mCAAmC;AACvC;;AAEA;IACI,WAAW;IACX,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,qCAAqC;AACzC;;AAEA;IACI,wCAAwC;IACxC,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,YAAY;IACZ,kBAAkB;AACtB;;AAEA;IACI,iCAAiC;IACjC,8BAA8B;IAC9B,SAAS;AACb;;AAEA;IACI,aAAa;IACb,eAAe;IACf,uBAAuB;IACvB,mCAAmC;IACnC,WAAW;IACX,2EAA2E;AAC/E;;AAEA;IACI,kBAAkB;IAClB,aAAa;IACb,mBAAmB;IACnB,YAAY;IACZ,6BAA6B;IAC7B,YAAY;IACZ,iBAAiB;IACjB,mCAAmC;IACnC;;wCAEoC;IACpC,iBAAiB;IACjB,+BAA+B;AACnC;;AAEA;IACI,UAAU;AACd;;AAEA;IACI,gBAAgB;AACpB;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,YAAY;IACZ,sBAAsB;IACtB,aAAa;AACjB;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,uBAAuB;IACvB,WAAW;IACX,YAAY;IACZ,sCAAsC;IACtC,YAAY;IACZ,cAAc;IACd,UAAU;AACd;;AAEA;IACI,2CAA2C;AAC/C;;AAEA;IACI,4CAA4C;AAChD;;AAEA;IACI,4CAA4C;IAC5C,+CAA+C;AACnD;;AAEA;IACI,WAAW;IACX,qBAAqB;IACrB,8DAA8D;IAC9D,WAAW;IACX,YAAY;AAChB;;AAEA;IACI,WAAW;IACX,qBAAqB;IACrB,gEAAgE